.Button {
  font-size: large;
  height: 36px;
  padding: 2px 12px;
  border: 0;
  border-radius: 4px;
  color: #fff;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.3);;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#336a88), to(#2a5871));;
  background-image: -webkit-linear-gradient(top, #336a88, #2a5871);;
  background-image: -o-linear-gradient(top, #336a88, #2a5871);;
  background-image: linear-gradient(to bottom, #336a88, #2a5871);
  margin-right: 6px;
}

.Button:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#22475b), to(#183240));
  background-image: -webkit-linear-gradient(top, #22475b, #183240);
  background-image: -o-linear-gradient(top, #22475b, #183240);
  background-image: linear-gradient(to bottom, #22475b, #183240);
}

.Button--red {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#e93031), to(#b72626));
  background-image: -webkit-linear-gradient(top, #e93031, #b72626);
  background-image: -o-linear-gradient(top, #e93031, #b72626);
  background-image: linear-gradient(to bottom, #e93031, #b72626);
}

.Button--red:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ff3435), to(#d52b2b));
  background-image: -webkit-linear-gradient(top, #ff3435, #d52b2b);
  background-image: -o-linear-gradient(top, #ff3435, #d52b2b);
  background-image: linear-gradient(to bottom, #ff3435, #d52b2b);
}



